import React ,{ useState } from "react";

export default ({ onSave,onData }) => {
  const [selectedElement, setSelectedElement] = useState(""); // état local pour stocker l'élément sélectionné


  const onDragStart = (event, nodeType) => {
    event.dataTransfer.setData("application/reactflow", nodeType);
    event.dataTransfer.effectAllowed = "move";
    event.dataTransfer.setData("elm", selectedElement);
    
  };

  const [isOpen, setIsOpen] = useState(false);
  function close() {
    setIsOpen(false);
  }
  return (
    <aside>
      <div className="description">
        <p>Générer le code d'un modèle en déplaçant les blocs dans le canvas</p>
        
        <button onClick={onData}>New</button>
        <button onClick={onSave}>Save</button>
      </div>
      <div
        onMouseEnter={() => setSelectedElement("DataSource")}
        className="dndnode input"
        onDragStart={(event) => onDragStart(event, "input")}
        draggable
      >
        DataSource
      </div>
      <div onMouseEnter={() => setSelectedElement("Time Series Split")}
        className="dndnode"
        onDragStart={(event) => onDragStart(event, "default")}
        draggable
      >
       Time Series Split
      </div>

      <div
        onMouseEnter={() => setSelectedElement("Features")}
        className="dndnode"
        onDragStart={(event) => onDragStart(event, "default")}
        draggable
      >
        Features
      </div>
      <div
        onMouseEnter={() => setSelectedElement("Dummies")}
        className="dndnode"
        onDragStart={(event) => onDragStart(event, "default")}
        draggable
      >
        Dummies 
      </div>
      
      <div
        onMouseEnter={() => setSelectedElement("Standard Scaler")}
        className="dndnode"
        onDragStart={(event) => onDragStart(event, "default")}
        draggable
      >
        Standard Scaler 
      </div>
      
      <div
        onMouseEnter={() => setSelectedElement("Pandas qcut")}
        className="dndnode"
        onDragStart={(event) => onDragStart(event, "default")}
        draggable
      >
       Pandas qcut
      </div>
      
      <div
        onMouseEnter={() => setSelectedElement("Target Encoding")}
        className="dndnode"
        onDragStart={(event) => onDragStart(event, "default")}
        draggable
      >
       Target
      </div>
      
      <div
        onMouseEnter={() => setSelectedElement("xgBoost")}
        className="dndnode "
        onDragStart={(event) => onDragStart(event, "default")}
        draggable
      >
       xgBoost
      </div>

      <div
        onMouseEnter={() => setSelectedElement("Scores")}
        className="dndnode output"
        onDragStart={(event) => onDragStart(event, "output")}
        draggable
      >
       Scores
      </div>

      
      
    </aside>
  );
};
