import React, {useRef, useEffect, useState,useCallback  } from "react"
import { ComponentProps, Streamlit, withStreamlitConnection } from "streamlit-component-lib"
import ReactFlow, {
    ReactFlowProvider,
    Controls,
    Background,
    addEdge,
    removeElements    
  } from "react-flow-renderer";
  
import Sidebar from "./Sidebar.js";

import {ReactComponent as ReactLogo} from "./cog.svg";

/** 
 * We can use a Typescript interface to destructure the arguments from Python
 * and validate the types of the input
 */


const initialElements = [

  ];

  const generateNodesFromValueChain = (elms) => {
    const elements = [];
    for (const i of Object.keys(elms)) {
      console.log(elms[i])
      elements.push(elms[i]);
    }
    return elements;
  };
  
const generateEdgesFromValueChain = (valuChain) => {
    const elements = [];
    for (const valuChainItem in valuChain) {
      if (Object.hasOwnProperty.call(valuChain, valuChainItem)) {
        const element = valuChain[valuChainItem];
        if (element.childOf) {
          elements.push({
            id: `edge-${valuChainItem}-${element.childOf}`,
            source: valuChainItem,
            target: element.childOf,
            type: "smoothstep"
          });
        }
      }
    }
    return elements;
  };
  
  let id = 0;
  const getId = () => `dndnode_${id++}`;

/**
 * No more props manipulation in the code.
 * We store props in state and pass value directly to underlying Slider
 * and then back to Streamlit.
 */



const Labs = function Labs(props) {
  const [paneMoveable] = useState(false);
  const [panOnScroll] = useState(true);
  const reactFlowWrapper = useRef(null);
  const [reactFlowInstance, setReactFlowInstance] = useState(null);
  const [elements, setElements] = useState(initialElements);

  const clinetData = JSON.parse(props.args.json);
  // if sizof clinetData.valueChain > 0
  
  let onLoad = (_reactFlowInstance) => {
    setReactFlowInstance(_reactFlowInstance);
  };

  if(clinetData.length > 0){
    onLoad = (_reactFlowInstance) => {
      setReactFlowInstance(_reactFlowInstance);

      // Set Nodes
      setElements((es) =>
        es.concat(generateNodesFromValueChain(clinetData))
      );
      // Set Edges
      setElements((es) =>
        es.concat(generateEdgesFromValueChain(clinetData))
      );
    };
  }else{
  }
 
  

  function newData(){
      setElements((els) => els.filter((element) => element.id == ""));
  } 

  function save(){
    
    if( elements.length > 0){
      const s = prompt("Entrez le nom de votre fihcier")
      if(s!==""){
        const elms = reactFlowInstance.getElements();
        console.log(elms,s);
        Streamlit.setComponentValue(JSON.stringify(elms, null, 2),s)
      }
    }
  }
  const onConnect = (params) => setElements((els) => addEdge(params, els));
  const onElementsRemove = (elementsToRemove) =>
    setElements((els) => removeElements(elementsToRemove, els));

    
  
    const onNodeDoubleClick = (event, node) => {
      setElements((els) => els.filter((element) => element.id !== node.id));
    };
    

  const onDragOver = (event) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = "move";
  };

  const onDrop = (event) => {
    event.preventDefault();
    console.log(event.dataTransfer);
    const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
    const type = event.dataTransfer.getData("application/reactflow");
    const elm  = event.dataTransfer.getData("elm");
    const position = reactFlowInstance.project({
      x: event.clientX - reactFlowBounds.left,
      y: event.clientY - reactFlowBounds.top
    });

    
    

    const newNode = {
      id: getId(),
      elm,
      type,
      position,
      sourcePosition: "right",
      targetPosition: "left",
      data: { label: elm }
    };

    setElements((es) => es.concat(newNode));
    
    
  };

  
  
  
    
    // Destructure using Typescript interface
    // This ensures typing validation for received props from Python
    const { label, initialValue } = props.args
    const [value, setValue] = useState(initialValue)
    useEffect(() => Streamlit.setFrameHeight())

  const [elementsjson, setElementsjson] = useState([]);
  
 

  return (
    <>

    <div className="dndflow">
      <ReactFlowProvider>
        <div className="reactflow-wrapper" ref={reactFlowWrapper}>
          <ReactFlow
            elements={elements}
            onConnect={onConnect}
            onLoad={onLoad}
            onDrop={onDrop}
            onDragOver={onDragOver}
            paneMoveable={paneMoveable}
            panOnScroll={panOnScroll}
            snapToGrid={true}
            onNodeDoubleClick={onNodeDoubleClick}
          >
            
            <Controls />
            <Background variant="lines" />
            
          </ReactFlow>
        </div>
        <Sidebar onSave={save} onData={newData}> 
        </Sidebar>
        
      </ReactFlowProvider>
    </div>
    </>
  )
}

export default withStreamlitConnection(Labs)
